import React from 'react';
import firebase from 'firebase';

import * as Spirits from '../resources/spirits';

import SpiritCheckbox from '../components/SpiritCheckbox';

class AddCocktailForm extends React.Component {
  state = {
    name: '',
    url: '',
    selectedSpirits: 0,
    error: null,
  }

  handleSubmit = (e) => {
    e.preventDefault();
    const cocktailsRef = firebase.database().ref('cocktails');
    const { name, url, selectedSpirits } = this.state;

    cocktailsRef.push({
      name,
      URL: url,
      spirits: selectedSpirits,
    }, (error) => {
      if (!error) {
        this.setState({
          name: '',
          url: '',
          selectedSpirits: 0,
        });
      } else {
        this.setState({ error });
      }
    });
  };

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  handleCheckboxChange = (e) => {
    if (e.target.checked) {
      this.setState({
        selectedSpirits: this.state.selectedSpirits | e.target.value,
      });
    } else {
      this.setState({
        selectedSpirits: this.state.selectedSpirits & ~e.target.value,
      });
    }
  }

  render() {
    const { name, url, selectedSpirits, error } = this.state;
    return (
      <form onSubmit={this.handleSubmit}>
        <h2>Add Cocktail</h2>
        <input type="text" name="name" className="form-field" value={name} onChange={this.handleChange} placeholder="Cocktail Name" required />
        <input type="url" name="url" className="form-field" value={url} onChange={this.handleChange} placeholder="Link to recipe" required />
        <div>
          {Object.keys(Spirits)
            .map(key => (
              <SpiritCheckbox
                key={key}
                name={key}
                prefix="AddCocktailForm_"
                value={Spirits[key]}
                onChange={this.handleCheckboxChange}
                checked={!!(selectedSpirits & Spirits[key])}
              />
            ))
          }
        </div>
        <div>
          <button type="submit">Save</button>
        </div>
        {error && (
          <p>{error.message}</p>
        )}
      </form>
    );
  }
}

export default AddCocktailForm;



